.n-wrapper{
    height: 10vh;
    display: flex;
    justify-content: space-between;
}
.n-leftside{
    flex: 1;
    align-items: center;
    display: flex;
    gap: 2rem;
}
.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}
.n-rightside{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}
.n-list{
    flex: 10;
}
.n-list > ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    cursor: pointer;
}
.n-list > ul > li:hover{
    cursor: pointer;
    color: var(--primary);
}
.n-button{
    flex: 2;
}
@media screen and (max-width: 480px) {
    .n-list {
      display: none;
    }
  }